"use client";
import React, { useState } from "react";

function Header2() {
  // state for menu visibility
  const [isOpen, setIsOpen] = useState(false);

  return (
    <>
      <div className="header-2">
        <div className="mad-header-section">
          <div className="container-fluid">
            <div className="row">
              {/* Browse Categories */}
              <div className="col-xl-3 col-sm-5">
                <div id="mad-browse-cat" className="mad-browse-cat">
                  <button
                    id="mad-cat-toggle"
                    className="mad-cat-toggle"
                    onClick={() => setIsOpen(!isOpen)}
                  >
                    <span>Browse</span> Categories
                  </button>

                  {/* menu toggles open/close */}
                  <ul
                    className={`mad-cat-menu ${
                      isOpen ? "menu-open" : "menu-closed"
                    }`}
                  >
                    <li>
                      <a href="#">
                        <b>Special Deals</b>
                      </a>
                    </li>
                    <li>
                      <a href="#">
                        <b>Best Sellers</b>
                      </a>
                    </li>
                    <li>
                      <a href="#">
                        <b>New Arrivals</b>
                      </a>
                    </li>
                  </ul>
                </div>
              </div>

              {/* Search Section */}
              <div className="col-xl-6 col-md-7">
                <div className="mad-search-section">
                  <div className="mad-col">
                    <input type="text" placeholder="Search product" />
                  </div>

                  <div className="mad-col">
                    <div className="mad-custom-select">
                      <select defaultValue="">
                        <option value="" disabled>
                          All categories
                        </option>
                        <option>Vitamins &amp; Supplements</option>
                        <option>Personal Care &amp; Beauty</option>
                        <option>Medicines</option>
                        <option>Health &amp; First Aids</option>
                        <option>Sexual Health</option>
                        <option>Weight Loss &amp; Fitness</option>
                        <option>Mum &amp; Baby</option>
                        <option>Organic &amp; Gluten Free</option>
                        <option>Home &amp; Pets</option>
                        <option>Travel</option>
                        <option>Perfumes &amp; Gifts</option>
                      </select>
                    </div>
                  </div>

                  <button type="submit">
                    <i className="material-icons">search</i>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Header2;
